import { getLeavesClient } from '@mintlify/http-client';
import type { ServerResponse } from 'http';

import { IS_MULTI_TENANT } from '@/env';

function generateRobotsTxt(hostingLocation: string) {
  return `User-agent: *
Sitemap: ${hostingLocation}/sitemap.xml`;
}

function RobotsTxt() {}

export async function getServerSideProps({
  params: { subdomain },
  res,
}: {
  params: { subdomain: string };
  res: ServerResponse;
}) {
  if (!IS_MULTI_TENANT) {
    return {
      notFound: true,
    };
  }
  try {
    const hostingLocation = await getLeavesClient(subdomain).getHostingLocation();

    const robotsTxt = generateRobotsTxt(hostingLocation);

    res.setHeader('Content-Type', 'text/plain');
    res.write(robotsTxt);
    res.end();

    return {
      props: {},
    };
  } catch {
    return {
      notFound: true,
    };
  }
}

export default RobotsTxt;
